<template>
  <div id="app">
    <AppHeader :user="user"/>
    <!--   hideContent true  AppContent  -->
    <!--     AppContent  -->
    <!-- <AppContent v-if="!$route.meta.hideContent && loaded " :user="user"/> -->
    <router-view :user="user"/>
    <AppFooter />
  </div>
</template>

<script>
import AppHeader from './components/common/Header.vue'
// import AppContent from './components/common/Content.vue'
import AppFooter from './components/common/Footer.vue'

export default {
  name: 'App',
  components: {
    AppHeader,
    // AppContent,
    AppFooter
  },
  data() {
    return {
      user: { name: '', role: 'guest', email: '', isLoggedIn: false},
      loaded: false,
    };
  },
  async mounted() {
    await this.checkLogin();
  },
  methods: {
    async checkLogin() {
      const token = localStorage.getItem('accessToken');
      if (token!=null) {
        //        
        this.user.isLoggedIn = true;
        try {
          const response = await this.$axios.get('/main', {
            headers: {
              Authorization: token, // Bearer  
            },
          });
          this.user.role = response.data.role;
          this.user.name = response.data.name;
          this.user.email = response.data.email;
        } catch (error) {
          console.error('     :', error);
        }
      } else {
        //    
        this.user.isLoggedIn= false;
        this.user.role='guest';
      }
      this.loaded = true
    },
  },
}
</script>

<style>
#app {
  min-height: 100vh;  /*     */
  display: flex;
  flex-direction: column;  /*   */
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

*, *::before, *::after {
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
}

body, ul, li {
  list-style: none;  /* ul    */
}

a {
  text-decoration: none; /* a    */
}

:root {
  --header-height: 168px; /* Header    */
  --footer-height: 0px; /* Footer    */
}
</style>
