  color: white;
  /*   () */
  padding: 7px 14px;
  /*    ( 12px,  24px) */
  text-align: center;
  /*    */
  text-decoration: none;
  /*   */
  display: inline-block;
  /*     */
  font-size: 20px;
  /*   */
  border-radius: 15px;
