<template>
    <div class="reservations-view-page">
      <!--  1      -->
      <LogoHeader />
  
      <!--  ,  ,   -->
      <div class="menu">
        <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/reservations' }" @click="navigateTo('/mypage/reservations')"> </div>
        <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/membership' }" @click="navigateTo('/mypage/membership')"> </div>
        <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/edit-profile' }" @click="navigateTo('/mypage/edit-profile')">  </div>
      </div>
    
      <!--     -->
      <div class="reservations-view-content">
        <h2> </h2>
        <div v-if="reservations.length === 0" class="no-reservations">
            .
        </div>
        <div v-else class="reservation-table-wrapper">
          <table class="reservation-table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th>  </th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="reservation in paginatedReservations" :key="reservation.id">
                <td>{{ reservation.eventName }}</td>
                <td>{{ reservation.date }}</td>
                <td>{{ reservation.time }}</td>
                <td>{{ reservation.seat }}</td>
                <td><button @click="cancelReservation(reservation.id)" class="cancel-button"> </button></td>
              </tr>
            </tbody>
          </table>
          <div class="pagination-controls">
            <button @click="prevPage" :disabled="currentPage === 1"></button>
            <span>{{ currentPage }} / {{ totalPages }}</span>
            <button @click="nextPage" :disabled="currentPage === totalPages"></button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import LogoHeader from '../common/LogoHeader.vue';
  
  export default {
      name: 'ReservationsView',
      components: {
      LogoHeader
    },    
    data() {
      return {
        activeMenu: this.$route.path, //   
        reservations: [
          //  
          { id: 1, eventName: ' ', date: '2024-11-10', time: '15:00', seat: 'A3-12' },
          { id: 2, eventName: ' ', date: '2024-11-15', time: '19:00', seat: 'B1-08' },
          { id: 3, eventName: ' ', date: '2024-11-20', time: '18:00', seat: 'C2-05' },
          { id: 4, eventName: '', date: '2024-11-25', time: '20:00', seat: 'D4-10' },
          //  
        ],
        currentPage: 1,
        itemsPerPage: 2,
      };
    },
    computed: {
      totalPages() {
        return Math.ceil(this.reservations.length / this.itemsPerPage);
      },
      paginatedReservations() {
        const start = (this.currentPage - 1) * this.itemsPerPage;
        const end = start + this.itemsPerPage;
        return this.reservations.slice(start, end);
      },
    },
    watch: {
      //    activeMenu 
      $route(to) {
        this.activeMenu = to.path;
      }
    },
    methods: {
      navigateTo(route) {
        this.$router.push(route);
        this.activeMenu = route; //      
      },
      cancelReservation(id) {
        //   
        this.reservations = this.reservations.filter(reservation => reservation.id !== id);
        alert(' .');
        if (this.currentPage > this.totalPages) {
          this.currentPage = this.totalPages;
        }
      },
      nextPage() {
        if (this.currentPage < this.totalPages) {
          this.currentPage++;
        }
      },
      prevPage() {
        if (this.currentPage > 1) {
          this.currentPage--;
        }
      },
    }
  }
  </script>
  
  <style>
  .reservations-view-page {
      padding-top: var(--header-height);
      padding-bottom: var(--footer-height);
      text-align: center;
  }
  
  /*  */
  .menu {
    display: flex;
    justify-content: center;
    gap: 50px;
    padding-bottom: 20px;
  }
  
  .menu-item {
    width: 120px;
    padding: 10px 0;
    position: relative;
    cursor: pointer;
    font-weight: bold;
    color: #565656;
  }
  
  .menu-item::after {
    content: "";
    position: absolute;
    bottom: -5px; /*       */
    left: 0;
    width: 100%;  /*   div   */
    height: 3px;
    background-color: black;
    transform: scaleX(0); /*    */
    transition: transform 0.3s ease;
  }
  
  .active-menu-item {
    color: black;  /*     black  */
  }
  
  .active-menu-item::after {
    transform: scaleX(1);  /*     */
  }
  
  .menu-item:hover::after {
    transform: scaleX(1);  /*     */
  }
  
  /*   */
  .no-reservations {
    font-size: 18px;
    color: #999;
  }

  .reservation-table-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }

  .reservation-table {
    width: 80%;
    max-width: 800px;
    border-collapse: collapse;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
  }

  .reservation-table th, .reservation-table td {
    border: 1px solid #ccc;
    padding: 15px;
    text-align: center;
  }

  .reservation-table th {
    background-color: #f5f5f5;
    font-weight: bold;
  }

  .cancel-button {
    background-color: #d9534f;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .cancel-button:hover {
    background-color: #c9302c;
  }

  .pagination-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .pagination-controls button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .pagination-controls button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }

  .pagination-controls span {
    font-size: 16px;
    font-weight: bold;
  }
  </style>