            </div>


            <!--   -->
            <div v-if="firstPage">
        <div class="modal-body">
            <!--   -->
            <div class="modal-ticket-img" v-if="selectedZoneImage">
                <img :src="selectedZoneImage" alt="  " class="stadium-image" />
            </div>

            <!--   -->
            <div class="modal-table-container">
                        <table class="modal-table">
                            <thead class="table-theader">
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="zone in filteredAvailableSeats" :key="zone.zoneName" @click="selectZone(zone)">
                                    <td><input class="select-zone-btn" type="checkbox" v-model="zone.isChecked" 
                                        @change="onCheckboxChange"/>
                                        {{ zone.zoneName }}</td>
                                    <td></td>
                                    <td>{{ zone.availableSeatAmount }}</td>
                                </tr>
                            </tbody>
                        </table>

                        <!--     -->
                        <!-- <div v-if="isChecked" v-for="zone in selectedZones" :key="zone.zoneName">
                            <h3>{{ zone.zoneName }}  </h3> -->
                            <table v-if="isChecked" class="modal-table-hide">
                                <!-- <thead class="table-theader">
                                    <tr>
                                        <th></th>
                                        <th> </th>
                                        <th></th>
                                    </tr>
                                </thead> -->
                                <tbody>
                                    <tr v-for="section in zone.sections" :key="section.seatId">
                                        <td>{{ section.sectionName }}</td>
                                        <td>
                                            <div class="quantity-selector">
                                                <button @click="decreaseQuantity(section.seatId)" :disabled="section.availableSeatAmount <= 0">-</button>
                                                <input type="text" v-model="section.quantity" readonly class="quantity-box" />
                                                <button @click="increaseQuantity(section.seatId, section.availableSeatAmount)">+</button>
                                            </div>
                                        </td>
                                        <td>{{ section.availableSeatAmount }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        <!-- </div> -->
                    </div>
                </div>
                <hr class="divider" />

        <div class="result-text">
                .
            <div v-for="zone in selectedZones" :key="zone.zoneName">
                  : {{ zone.zoneName }} {{ zone.quantity }}
            </div>
            <div class="result-text-all">
                 : {{ countTicket.count }}
            </div>
        </div>
        <div class="first-button">
            <!--   -->
            <button class="buy-button" @click="openSecondPage"></button>

            <!--   -->
            <button class="close-button" @click="closeModal"></button>
        </div>
    </div>
            <!-- ------------------------------------------------------------------ -->

            <!-- 2   -->
            <div v-if="secondPage" class="second-modal-content">
                <div class="modal-body">

                    <!--   -->
                    <div class="left-table">
                        <div class="modal-table-container">
                            <table class="modal-table">
                                <thead class="table-theader">
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                </thead>

                                <!-- DB     -->
                                <tbody>
                                    <tr v-for="zone in selectedZones" :key="zone.name">
                                        <td> {{ zone.name }} </td>
                                        <td> {{ zone.quantity }}</td>
                                        <td> {{ zone.price }}</td>
                                        <td></td>
                                    </tr>
                                </tbody>
                            </table>

                            <table class="modal-table">
                                <thead class="table-theader">
                                    <tr>
                                        <td colspan="4"></td>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td colspan="4">{{ formattedTotal }}</td>
                                    </tr>
                                </tbody>
                            </table>

                            <div class="modal-info-box">
                                <div class="icon">
                                    <img src="@/assets/img/anyImg/bell-icon.png" alt="alert" />
                                </div>
                                <div class="info-content">
                                    <h4> </h4>
                                    <ul>
                                        <li> -     1  </li>
                                        <li> -           .</li>
                                    </ul>
                                    <h4> </h4>
                                    <p class="p">   12  
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="vertical-divider"></div>

                    <!--   -->
                    <div class="right-table">

                        <!--   -->
                        <!--      -->
                        <div class="modal-second-title">
                            <h2>{{ formatMatchInfo(matchInfo.matchTeam, thisTeam) }}</h2>
                            <div class="match-info">
                                <p>{{ matchInfo.matchStadium }}</p>
                                <p> | </p> 
                                <p v-html="formatDate(matchInfo.matchDate)"></p>
                            </div>
                        </div>
                        <div>
                            
                        </div>
                        <div v-for="zone in selectedZones" :key="zone.name">
                            {{ zone.name }} / {{ zone.quantity }}
                        </div>
                        <hr class="divider" />

                        <div>
                            <table class="second-modal-table">
                                <tr>
                                    <td> </td>
                                    <td>
                                        {{ formattedTotal }}
                                    </td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td>
                                        
                                    </td>
                                </tr>
                                <tr>
                                    <td>  </td>
                                    <td>
                                        {{ formattedTotal }}
                                    </td>
                                </tr>
                            </table>
                            <br>
                            <table class="second-modal-table">
                                <tr>
                                    <td> </td>
                                    <td> </td>
                                </tr>
                                <tr>
                                    <td> -   </td>
                                    <td>  </td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> </td>
                                </tr>
                            </table>

                        </div>
                        <div>
                            <p class="p">
                                 :  [12 ]<br>
                                <!--  -->
                                 : []
                            </p>
                        </div>
                        <div class="second-button">
                            <!--   -->
                            <button class="buy-button" @click="openThirdPage"> </button>
                            <!--   -->
                            <button class="close-button" @click="openFirstPage"></button>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'TicketModal',

    components: {

    },

    props: {
        visible: {
            type: Boolean,
            default: false,
        },
        user: {
            type: Object,
            default: () => ({ name: '', role: 'guest', email: '', isLoggedIn: false })
        },
        match: {
            type: Object,
            required: true,
        },
        
    },
    async mounted() {
        console.log("Mounted with user:", this.user); //   
        await this.fetchEvents(); //  fetch
    },

    computed: {
        selectedZones() {
            //  1   
            return this.availableSeatsList
                .filter(zone => zone.availableSeatAmount > 0 && zone.isChecked)
                .map(zone => ({
                    ...zone,
                    sections: zone.sections || [],
                    quantity: zone.sections.reduce((total, section) => total + (section.quantity || 0), 0), //   
                    price: zone.price * zone.quantity //  
                }));
        },
        filteredAvailableSeats() {
            return this.availableSeatsList.filter(zone => zone.availableSeatAmount > 0);
        },

        formattedTotal() {
            //       
            return this.total.toLocaleString();
        },

        formattedPayment() {
            return this.payment.toLocaleString();
        },

    },

    watch: {
        //    ,   0 activeButtonIndex 
        zones: {
            handler() {
                if (this.zones.every(zone => zone.quantity === 0)) {
                    this.activeButtonIndex = null;
                }
            },
            deep: true
        }
    },

    data() {
        return {
            //   
            firstPage: true,
            secondPage: false,

            //   
            ticketSalesDto: {},
            userBalance: 0,
            matchInfo: {},
            availableSeatsList: [],
            userMembership: {},
            seatDTOList: [],

            // 
            selectedZoneImage: require('@/assets/img/stadium/stadium-main.jpg'),
            countTicket: {
                count: 0,
            },
            total: 0,
            thisTeam: "GS ITM",
            zones: []  //  
        };
    },

    methods: {
        // API 
        async fetchEvents() {
            // let data = {};
            try {
                console.log("Sending request with:", {
                    email: this.user.email,
                    matchId: this.match.matchId
                });
                const response = await axios.get('/ticket/purchase/modal', {
                    params: {
                        email: this.user.email,
                        matchId: this.match.matchId
                    }
                });
                this.ticketSalesDto = response.data.ticketSalesDto;
                this.userBalance = response.data.userBalance;
                this.matchInfo = response.data.matchInfo;
                this.availableSeatsList = response.data.availableSeatsList;
                this.userMembership = response.data.userMembership;
                this.seatDTOList = response.data.seatDTOList;
                this.initializeSectionQuantities(); //   
                console.log(response.data);
                console.log("response:", response);
                // response.data = response.data;
            } catch (error) {
                console.error("Error fetching data: ", error);
            }
        },
        //     
        formatMatchInfo(matchTeam, thisTeam) {
            return matchTeam === "" ? `${thisTeam} VS ${matchTeam}` : `${matchTeam} VS ${thisTeam}`;
        },
        //   
        formatDate(date) {
            const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
            const dateObj = new Date(date);
            
            //  
            const formattedDate = dateObj.toLocaleDateString('ko-KR', options);
            
            //  
            const days = ['', '', '', '', '', '', ''];
            const dayName = days[dateObj.getDay()];

            //  
            const formattedTime = dateObj.toLocaleTimeString('ko-KR', {
            hour: '2-digit',
            minute: '2-digit'
            });

            return `${formattedDate} (${dayName}) ${formattedTime}`; // <br> 
        },

        // 
        calculateTotal() {
            for (let i = 0; i < this.selectedZones.length; i++) {
                this.total = this.selectedZones[i].price * this.selectedZones[i].quantity + this.total;
            }
        },

        //   
        openFirstPage() {
            this.firstPage = true;
            this.secondPage = false;
            this.total = 0;
        },

        //   
        openSecondPage() {
            this.calculateTotal();
            if (this.total > 0) {
                this.firstPage = false;
                this.secondPage = true;
            } else {
                alert(" .");
            }
        },


        onCheckboxChange() {
            if (this.isChecked) {
                this.showDetails();
            } else {
                this.hideDetails();
            }
        },
        showDetails() {
        },
        hideDetails() {
            this.countTicket.count = 0;
            this.zones.forEach(zone => {
                zone.quantity = 0; //    0                 
            });
            this.selectedZoneImage =
                require('@/assets/img/stadium/stadium-main.jpg'); //  
        },

        //   
        closeModal() {
            this.hideDetails();
            this.isChecked = false;
            this.$emit('close');
        },
        openModal() {
            this.isModalOpen = true;
        },
        //   
        selectZone(zone) {
            if (zone && typeof zone.isChecked !== 'undefined') {
                //     
                this.zones.forEach(z => {
                    if (z.zoneName !== zone.zoneName) {
                        z.isChecked = false; //  
                    }
                });

                //   
                zone.isChecked = !zone.isChecked;

                if (zone.isChecked) {
                    //   selectedZones 
                    const existingZone = this.selectedZones.find(z => z.zoneName === zone.zoneName);
                    if (!existingZone) {
                        this.selectedZones = [{
                            zoneName: zone.zoneName,
                            sections: zone.sections, //    
                            quantity: 0 //  
                        }];
                    }
                } else {
                    //    selectedZones 
                    this.selectedZones = this.selectedZones.filter(z => z.zoneName !== zone.zoneName);
                }
            }
        },
        initializeSectionQuantities() {
            //    
            this.availableSeatsList.forEach(zone => {
                zone.sections.forEach(section => {
                    section.quantity = 0; //  
                });
            });
        },
        increaseQuantity(seatId, availableSeatAmount) {
            const section = this.selectedZones.flatMap(zone => zone.sections).find(section => section.seatId === seatId);
            if (section && section.quantity < availableSeatAmount) {
                section.quantity++;
            }
        },
        decreaseQuantity(seatId) {
            const section = this.selectedZones.flatMap(zone => zone.sections).find(section => section.seatId === seatId);
            if (section && section.quantity > 0) {
                section.quantity--;
            }
        },
    },
}
</script>


