<template>
  <header>
    <!-- ,    -->
    <div class="auth-links">
      <router-link v-if="!user.isLoggedIn" to="/login" class="login"></router-link>
      <router-link v-if="!user.isLoggedIn" to="/signup" class="signup"></router-link>
      <button v-else @click="logout" class="logout"></button>
    </div>
  
    <!--   -->
    <nav class="navbar">
      <div class="nav-menu">
        <!--   -->
        <ul class="nav-left">
          <li class="nav-item" v-for="(item, index) in leftMenu" :key="index">
            <router-link :to="item.path">{{ item.title }}</router-link>
            <ul class="dropdown">
              <li v-for="subItem in item.submenu" :key="subItem">
                <router-link :to="subItem.path">{{ subItem.title }}</router-link>
              </li>
            </ul>
          </li>
        </ul>
  
        <!--   -->
        <div class="logo">
          <router-link to="/">
            <img src="@/assets/logo-mms.png" alt="Team Logo">
          </router-link>
        </div>
  
        <!--   -->
        <ul class="nav-right">
          <li class="nav-item" v-for="(item, index) in rightMenu" :key="index">
            <router-link :to="item.path">{{ item.title }}</router-link>
            <ul class="dropdown">
              <li v-for="subItem in item.submenu" :key="subItem.path">
                <router-link :to="subItem.path">{{ subItem.title }}</router-link>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </nav>
  </header>
  </template>
  
  <script>
  
  export default {
    name: 'AppHeader',
    props: {
      user: {
        type: Object,
        default: () => ({ name: '', role: 'guest', email: '', isLoggedIn: false })
      }
    },
    data() {
      return {
        leftMenu: [
        { 
          title: 'MMS', 
          path: '/mms/greeting',  //    
          submenu: [
            { title: '', path: '/mms/greeting' }, 
            { title: '', path: '/mms/history' }, 
            { title: 'CI', path: '/mms/ci' }, 
            { title: '', path: '/mms/sponsor' }
          ] 
        },
        { 
          title: 'TEAM',
          path: '/team/players', 
          submenu: [
            { title: '', path: '/team/players' }, 
            { title: '', path: '/team/staff' }, 
            { title: '', path: '/team/stadium' }
          ] 
        },
        {
          title: 'GAME', 
          path: '/game/schedule',
          submenu: [
            { title: ' ', path: '/game/schedule' },
            { title: ' ', path: '/game/results' },
          ]
        }
      ],
      rightMenu: [
        { 
          title: 'TICKET', 
          path: '/ticket/info',
          submenu: [
            { title: ' ', path: '/ticket/info' }, 
            { title: ' ', path: '/ticket/purchase' }
          ] 
        },
        { 
          title: 'MEMBERSHIP', 
          path: '/membership/info',
          submenu: [
            { title: ' ', path: '/membership/info' }, 
            { title: ' ', path: '/membership/purchase' }
          ] 
        },
        { 
          title: 'MY PAGE', 
          path: '/myPage/reservations',
          submenu: [
            { title: ' ', path: '/myPage/reservations' }, 
            { title: ' ', path: '/myPage/membership' }, 
            { title: '  ', path: '/myPage/edit-profile' }, 
          ] 
        }
      ]
    };
  },

  methods: {
    logout() {
    // localStorage  
    localStorage.removeItem('accessToken');
    
    //      
    const token = localStorage.getItem('accessToken');
    if (token === null) {
      console.log('  .');
    } else {
      console.log('  .', token);
    }
    
    //   
    this.$router.push('/');
  }
}
  }
</script>

<style scoped>
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

/* ,    */
.auth-links {
  display: flex;
  justify-content: flex-end;
  padding: 10px 20px;
  background-color: white;
}

.auth-links a {
  color: #565656;
  margin: 0 10px;
  font-size: 18px;
}

.auth-links a:hover {
  color: #000000;
}

/*   */
.logout{
  background-color: #ffffff;
  border: none; /*   */
  outline: none; /*      ( ) */
  font-size: 18px;
  color: #565656;
}

.logout:hover{
  color: #000000;
}

/*   */
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  background-color: white;
  border-bottom: 1px solid #ddd;
  width: 100%;
  box-shadow: 0px 8px 8px -4px rgba(0, 0, 0, 0.1);
}

/*     */
.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0;
  margin: 0;
}

/*   */
