  display: flex;
  justify-content: center;
  align-items: center;
}

.logo img {
  height: 100px;
}

/*   */
.nav-left {
  display: flex;
  justify-content: center; 
  flex-grow: 1;
  padding: 0;
}

/*   */
.nav-right {
  display: flex;
  justify-content: center; 
  flex-grow: 1;
  padding: 0;
}

/* 1  */
.nav-item {
  position: relative;
  justify-content: center;
  text-align: center;
  font-size: 18px;
  width: 160px;
}

.nav-item a {
  color: #565656;
  font-weight: 600;
}

/* 2  */
.dropdown {
  display: none;
  position: absolute;
  top: 100%;  /* 2  1     */
  left: 0;  /* 2  1     */
  width: 100%;  /*     */
  padding: 0;
  background-color: white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  font-size: 16px;
}

.nav-item:hover .dropdown {
  display: block;
}

.dropdown li a {
  padding: 10px 20px;
  display: block;
  color: #565656;
}

.dropdown li a:hover {
  color: #000000;
  background-color: #f2f2f2;
  border-radius: 5px;
}
</style>