<template>
  <div class="calendar-main">
      <!--    -->
      <h2 class="header-title">
          <!--    -->
          <span class="current-month">({{ selectedYear }} {{ selectedMonth }})</span>
          <router-link to="/game/schedule">
            <button class="view-all-btn">  </button>
          </router-link>
      </h2>

    <!--   -->
    <table class="calendar-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="week in calendarWeeks" :key="week[0].date">
          <td v-for="day in week" :key="day.date" :class="{ 'home-game': day.events.length && day.events[0].matchStadium === 'HOME', 'away-game': day.events.length && day.events[0].matchStadium === 'AWAY' }">
            <div class="day-number">{{ day.date }}</div>
            <div v-if="day.events.length">
              <div v-for="event in day.events" :key="event.id" class="event">
                <!--       -->
                <img :src="getTeamLogo(event.matchOpponentTeamId)" alt="Team Logo" class="team-logo" />
                <!--    -->
                <div class="event-info">
                  {{ formatLocationAndTime(event) }}
                </div>
                <!--    ( ) -->
                <div class="result-container">
                  <div :class="['result-circle', getResultClass(event.matchSetScore, event.matchOpponentTeamSetScore)]">
                    <span class="result">{{ getDisplayResult(event.matchSetScore, event.matchOpponentTeamSetScore) }}</span> <!-- / -->
                  </div>
                  <span class="score">{{ event.matchSetScore }}:{{ event.matchOpponentTeamSetScore }}</span> <!--  -->
                </div>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'CalendarMain',
  props: {
    events: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      selectedYear: new Date().getFullYear(),
      selectedMonth: new Date().getMonth() + 1,
    };
  },
  methods: {
    //   
    getTeamLogo(teamId) {
      switch (teamId) {
        case 1:
          return require('@/assets/img/game/team_id_1_ok.png');
        case 2:
          return require('@/assets/img/game/team_id_2_vixtorm.png');
        case 3:
          return require('@/assets/img/game/team_id_3_walkers.png');
        case 4:
          return require('@/assets/img/game/team_id_4_blueFangs.png');
        case 5:
          return require('@/assets/img/game/team_id_5_stars.png');
        case 6:
          return require('@/assets/img/game/team_id_6_won.png');
        default:
          return ''; //  (    )
      }
    },
    //    
    // HOME ->   14:00 / AWAY ->  14:00 ,   14:00
    formatLocationAndTime(match) {
      const date = new Date(match.matchDate);
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const time = `${hours}:${minutes}`;

      let location = '';

      // HOME    ' ' 
      if (match.matchStadium === 'HOME') {
        location = ' ';
      } else {
      // AWAY  
      const teamStadium = this.getTeamStadium(match.matchOpponentTeamId);
        // ,  -> '' 
        if (match.matchOpponentTeamId === 2) {
          location = '';
        } else if (match.matchOpponentTeamId === 5) {
          location = '';
        } else {
          //   AWAY    ' + '  ex.  
          location = teamStadium.slice(0, 2) + ' ' + teamStadium.slice(2, -3);
        }
      }
      return `${location} ${time}`;
    },
    //       API    ID   
    getTeamStadium(teamId) {
      switch (teamId) {
        case 1:
          return '';
        case 2:
          return '';
        case 3:
          return '';
        case 4:
          return '';
        case 5:
          return '';
        case 6:
          return '';
        default:
          return '';
      }
    },
    //    ,     
    getResultClass(matchSetScore, matchOpponentTeamSetScore) {
      if (matchSetScore > matchOpponentTeamSetScore) {
        return 'win';
      } else if (matchSetScore < matchOpponentTeamSetScore) {
        return 'lose';
      }
      return 'upcoming';
    },
    getDisplayResult(matchSetScore, matchOpponentTeamSetScore) {
      return matchSetScore > matchOpponentTeamSetScore ? '' : '';
    }
  },
  computed: {
    calendarWeeks() {
      const daysInMonth = new Date(this.selectedYear, this.selectedMonth, 0).getDate();
      const firstDayOfMonth = new Date(this.selectedYear, this.selectedMonth - 1, 1).getDay();
      const weeks = [];
      let days = [];
      
      //     
      for (let i = 0; i < firstDayOfMonth; i++) {
        days.push({ date: '', events: [] });
      }

      //   
      for (let day = 1; day <= daysInMonth; day++) {
        const date = `${this.selectedYear}-${String(this.selectedMonth).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
        
        // const dayEvents = this.events.filter(event => event.date === date);
        // days.push({ date: day, events: dayEvents });

        //  event.matchDate ISO     
        const dayEvents = this.events.filter(event => {
          const eventDate = new Date(event.matchDate).toISOString().split('T')[0];
          return eventDate === date;
        });

        days.push({ date: day, events: dayEvents });
        
        //  
        if (days.length === 7) {
          weeks.push(days);
          days = [];
        }
      }

      //     
      if (days.length > 0) {
        while (days.length < 7) {
          days.push({ date: '', events: [] });
        }
        weeks.push(days);
      }

      return weeks;
    }
  }
};
</script>

<style scoped>
.header-title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  height: 50px;
  margin: 0 auto 20px auto;
  font-weight: bold;
  position: relative;
}

.current-month {
  color: gray;
  text-align: center;
  flex: 1;
}

.view-all-btn {
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  border: none;
  box-shadow: 0 2px 4px rgba(50, 50, 50, 0.2);
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  margin-left: 20px;
  transition: border-color 0.3s ease;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.calendar-table {
  width: 100%;
  max-width: 1200px;
  height: auto;
  margin: 0 auto;
  border-collapse: collapse;
}

.calendar-table thead {
  height: 50px; /*    */
  border-top: 2px solid #4f8578;
  border-bottom: 1px solid #ddd;
}

.calendar-table th {
  height: 50px; /*     */
  text-align: center;
  vertical-align: middle;
}

.calendar-table tbody {
  height: 1029px; /*    */
}

.calendar-table td {
  position: relative;
  width: 170.760px;
  height: 180px;
  border: 1px solid #ddd;
  text-align: center;
  vertical-align: top;
}

.day-number {
  position: absolute;
  top: 10px;
  left: 17px;
  font-size: 14px;
  color: #333;
}

.calendar-table td.home-game {
  /*    */
  background-color: #acc8c1;
}

.calendar-table td.away-game {
  /*    */
  background-color: #d9d9d9;
}

.event {
  display: flex;
  flex-direction: column; 
  justify-content: flex-end;
  align-items: center;  
  text-align: center; 
  width: 100%; 
  height: 100%; 
  margin-top: 13px;
}

.team-logo {
  width: 100%;
  margin-top: 10px;
  max-height: 60px;
  width: auto;
  margin-bottom: 10px;
}

.result-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

/* ,  */
.result-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #333; /*   */
  color: white;
  font-weight: bold;
  margin-left: auto;
}

/*     */
.win {
  background-color: #52a792;
}

/*     */
.lose {
  background-color: rgb(67, 67, 67);
}

/* ,  */
.result {
  font-size: 14px;
  text-align: center;
  align-items: center;
}

.score {
  font-size: 16px;
  font-weight: bold;
  margin-left: 10px;
}
</style>